.header {
    @include container;
    &__nav {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 64px 0;

        @include media($lg) {
            padding: 48px 0;
        }

        &-logo {
            width: 238px;
            height: 38px;

            @include media($xs) {
                width: 192px;
                height: 30px;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }

        &-list {
            display: flex;
            align-items: center;

            @include media($md) {
                display: none;
            }
        }

        &-item:not(:last-child) {
            margin-right: 70px;
        }

        &-link {
            color: $text;
            font-size: 20px;
            transition: 0.2s;

            @include media($xs) {
                font-size: 18px;
            }

            &:hover {
                color: $text_hover;
            }

            &.active {
                color: $extra;
            }
        }

        &-menu {
            display: none;
            font-size: 40px;
            cursor: pointer;
            position: relative;
            background: transparent;
            z-index: 2;

            @include media($xs) {
                font-size: 36px;
            }

            &:hover::before {
                color: $text_hover;
            }

            &.show .header__nav-list {
                visibility: visible;
                opacity: 1;
            }

            @include media($md) {
                display: block;
            }

            .header__nav-list {
                position: absolute;
                right: 8px;
                top: 32px;
                display: block;

                flex-direction: column;
                border-radius: 12px;
                overflow: hidden;
                z-index: 2;

                visibility: hidden;
                opacity: 0;
                transition: 0.2s;

                @include media($xs) {
                    right: 4px;
                }
            }

            .header__nav-item {
                margin-right: 0;
                display: flex;
                align-items: center;
                justify-content: center;
                background: $menu-bg;
                backdrop-filter: blur(5px);
                transition: 0.2s linear;

                &:hover {
                    background: $menu-bg_hover;
                }
            }

            .header__nav-link {
                padding: 24px 32px;
                min-width: max-content;
                width: 100%;

                @include media($xs) {
                    padding: 20px 24px;
                }
            }
        }
    }
}
