* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

ul {
    list-style: none;
}

button {
    border: none;
    outline: none;
}

.wrapper {
    background: $main;
    font-family: "Montserrat";
    background-image: url("../images/glass.png");
    *::before,
    *::after {
        font-family: "FontAwesome";
    }
    z-index: 1;
    position: relative;
}

.icon {
    &-plus::before {
        content: "\2b";
        color: $text;
        font-weight: 900;
    }

    &-search::before {
        content: "\f002";
        color: $text;
        font-weight: 900;
    }

    &-menu::before {
        content: "\f0c9";
        color: $text;
        font-weight: 900;
        transition: 0.2s;
    }

    &-telegram::before {
        content: "\f2c6";
        color: $text;
        font-weight: 500;
    }

    &-instagram::before {
        content: "\f16d";
        color: $text;
        font-weight: 500;
    }

    &-youtube::before {
        content: "\f167";
        color: $text;
        font-weight: 500;
        background: transparent;
    }

    &-arrow_right::before {
        content: "\f178";
        color: $green;
        font-weight: 900;
    }

    &-arrow_left::before {
        content: "\f177";
        color: $green;
        font-weight: 900;
    }
}

.page-title {
    font-family: "NeueMachina";
    font-size: 178px;
    font-weight: 400;
    color: $text;
    display: flex;
    flex-direction: column;
    position: relative;
    margin: 64px auto;
    align-items: flex-end;

    @include media($xl) {
        font-size: 160px;
    }

    @include media($lg) {
        font-size: 128px;
        margin: 48px auto;
    }

    @include media($md) {
        font-size: 96px;
        margin: 0 auto 48px;
    }

    @include media($sm) {
        font-size: 72px;
    }

    @include media($xs) {
        font-size: 48px;
        align-items: center;
    }

    span {
        background: $backlight;
        background-clip: text;
        -webkit-text-fill-color: transparent;
        margin-bottom: -50px;
        align-self: flex-start;

        @include media($lg) {
            margin-bottom: -24px;
        }

        @include media($sm) {
            margin-bottom: -16px;
        }

        @include media($xs) {
            margin-bottom: -8px;
            align-self: center;
        }
    }

    img {
        width: 154px;
        height: 154px;
        position: absolute;
        left: 30%;
        top: 35%;

        @include media($xl) {
            width: 128px;
            height: 128px;
            top: 40%;
            left: 28%;
        }

        @include media($lg) {
            width: 112px;
            height: 112px;
            top: 36%;
            left: 30%;
        }

        @include media($md) {
            width: 96px;
            height: 96px;
            top: 36%;
            left: 30%;
        }

        @include media($sm) {
            width: 72px;
            height: 72px;
        }

        @include media($xs) {
            width: 48px;
            height: 48px;
            top: 36%;
            left: auto;
            right: 10%;
        }
    }
}

.backlights {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;

    &-blur {
        width: min(823px, 45%);
        height: min(380px, 20rem);
        background: $backlight;
        border-radius: 823px;
        filter: blur(60px);
        position: absolute;
        opacity: 0.25;
    }

    &-blur:first-child {
        right: 15vh;
        top: 20vh;
    }

    &-blur:nth-child(2) {
        left: 0;
        top: 80vh;
    }

    &-blur:last-child {
        right: 5vh;
        top: 135vh;
    }
}

%button {
    color: $text;
    font-size: 20px;
    font-family: "Montserrat";
    font-weight: 500;
    border: 1px solid;
    border-radius: 100px;
    background: transparent;
    padding: 24px 48px;
    transition: 0.2s;
    cursor: pointer;

    &:hover {
        opacity: 0.75;
    }
}

%footer {
    &-title {
        color: $text;
        font-family: "Montserrat";
        font-size: 32px;
        font-weight: 600;

        @include media($lg) {
            font-size: 30px;
        }

        @include media($md) {
            font-size: 24px;
        }

        @include media($xs) {
            font-size: 21px;
        }
    }

    &-text {
        color: $extra;
        font-family: "Montserrat";
        font-size: 24px;
        font-weight: 400;

        @include media($lg) {
            font-size: 21px;
        }

        @include media($md) {
            font-size: 19px;
        }

        @include media($sm) {
            &:is(a):hover {
                color: $extra;
            }
        }

        @include media($xs) {
            font-size: 16px;
        }
    }
}

%products {
    &-price {
        color: $text;
        font-family: "Montserrat";
        font-size: 32px;
        font-weight: 600;
    }

    &-title {
        color: $text;
        font-family: "Montserrat";
        font-size: 24px;
        font-weight: 400;
    }
}
