  width: min(823px, 45%);
  height: min(380px, 20rem);
  background: linear-gradient(90deg, #01DE08 0%, #00FFE0 100%);
  border-radius: 823px;
  filter: blur(60px);
  position: absolute;
  opacity: 0.25;
}
.backlights-blur:first-child {
  right: 15vh;
  top: 20vh;
}
.backlights-blur:nth-child(2) {
  left: 0;
  top: 80vh;
}
.backlights-blur:last-child {
