.panel {
    @include container;
    padding-block: 64px;

    &__title {
        color: $text;
        font-family: "DrukWide";
        font-size: 60px;
        font-weight: 500;
        width: fit-content;
        margin: 0 auto;
        margin-bottom: 128px;
    }

    &__add {
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        &-title {
            @extend %footer-title;
            color: $product-bg;
            margin-bottom: 48px;
        }

        &-controls {
            width: 100%;
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            gap: 96px 128px;
        }

        &-descr {
            display: flex;
            align-items: flex-start;
            flex-direction: column;
        }

        &-name {
            margin-bottom: 32px;
        }

        &-name,
        &-category {
            position: relative;

            input {
                background: transparent;
                border: 2px solid $add-border;
                border-radius: 12px;
                height: 64px;
                width: 384px;
                color: $text;
                font-size: 20px;
                outline: none;
                transition: 0.2s linear;
                padding: 16px 24px;
            }

            input:focus {
                border-color: $detailed;
                box-shadow: 1px 1px 5px 5px $preview-shadow;
            }

            label {
                font-family: "Montserrat";
                color: $text-hover;
                font-size: 24px;
                position: absolute;
                top: 50%;
                left: 24px;
                transform: translateY(-50%);
                padding: 2px;
                background: transparent;
                transition: 0.2s;
                user-select: none;

                &.active {
                    font-size: 20px;
                    top: 0px;
                    color: $text;
                    backdrop-filter: blur(50px);
                }
            }
        }

        &-size {
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            justify-content: space-around;
            width: 100%;

            input {
                appearance: none;
                width: 0;
                height: 0;
                visibility: hidden;
                position: absolute;
                margin-left: -200vw;

                &:checked + label {
                    border-color: $text-hover;
                    color: $text-hover;
                    box-shadow: 1px 1px 5px 5px $preview-shadow;
                }
            }

            label {
                width: 80px;
                height: 56px;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 24px;
                font-family: "Montserrat";
                font-weight: 500;
                color: $add-border;
                background: transparent;
                border: 2px solid $add-border;
                border-radius: 16px;
                cursor: pointer;
                transition: 0.2s;
                margin-inline: 8px;
            }
        }

        &-price {
            background: transparent;
            border: 2px solid $add-border;
            border-radius: 12px;
            height: 64px;
            width: 268px;
            color: $text;
            font-size: 20px;
            outline: none;
            transition: 0.2s linear;
            padding: 16px 24px;

            &:focus {
                border-color: $detailed;
                box-shadow: 1px 1px 5px 5px $preview-shadow;
            }
        }

        &-delivery {
            display: flex;
            align-items: center;

            input {
                width: 24px;
                height: 24px;
                margin-right: 12px;
                
                &:checked + label {
                    color: $text;
                }
            }

            label {
                color: $text;
                font-size: 21px;
                color: $text-hover;
                transition: .2s;
            }
        }

        &-details {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            justify-content: space-around;
        }

        &-pictures {
            align-self: center;

            input {
                width: 0;
                height: 0;
                opacity: 0;
                visibility: hidden;
                position: absolute;
                margin-left: -200vw;
            }

            label {
                color: $blue;
                font-size: 16px;
                width: 416px;
                height: 64px;
                border-radius: 16px;
                border: 2px solid $blue;
                cursor: pointer;
                overflow: hidden;
                font-weight: 400;
                transition: .2s linear;

                display: flex;
                align-items: center;
                justify-content: space-between;

                &:first-of-type {
                    margin-bottom: 24px;
                }

                &:last-of-type {
                    color: $text;
                    border-color: $btn-text;
                    border-width: 3px;

                    div:last-of-type {
                        border-color: $btn-text;
                        background: $btn-text;
                        color: $text;
                    }
                }

                &:hover {
                    opacity: .8;
                }

                div {
                    padding-inline: 12px;
                }
                
                div:last-of-type {
                    border-left: 2px solid $blue;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    background: $blue;
                    color: $extra-bg;
                    font-weight: 500;
                }
            }
        }

        &-submit {
            @extend %button;
            width: fit-content;
            height: fit-content;
            justify-self: center;
            align-self: center;
            border-color: $green;
            color: $green;
        }

        &-advice {
            background: transparent;
            border: 2px solid $add-border;
            border-radius: 12px;
            min-height: 170px;
            height: 234px;
            width: 100%;
            color: $text;
            font-size: 20px;
            outline: none;
            transition: 0.2s linear;
            padding: 8px;
            resize: vertical;

            &:focus {
                border-color: $detailed;
                box-shadow: 1px 1px 5px 5px $preview-shadow;
            }
        }
    }
}
