.footer {
    padding: 96px 0 128px;
    background: $extra-bg;
    border-radius: 64px 64px 0 0px;

    @include media($lg) {
        padding: 64px 0 80px;
    }

    @include media($sm) {
        padding: 48px 0 64px;
        border-radius: 32px 32px 0 0px;
    }

    @include media($xs) {
        padding: 32px 0 48px;
        border-radius: 24px 24px 0 0px;
    }

    &__content {
        @include container;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;

        @include media($xl) {
            justify-content: space-around;
        }

        @include media($lg) {
            flex-wrap: wrap;
            justify-content: space-between;
        }
    }

    &__pages {
        @include media($lg) {
            display: flex;
            justify-content: space-between;
            width: 100%;
            margin-bottom: 48px;
        }

        @include media($sm) {
            flex-direction: column;
            align-items: center;
        }

        @include media($xs) {
            margin-bottom: 32px;
        }

        &-title {
            @extend %footer-title;
            margin-bottom: 64px;

            @include media($xl) {
                margin-bottom: 32px;
            }

            @include media($lg) {
                margin-bottom: 0;
            }

            @include media($sm) {
                margin-bottom: 24px;
            }

            @include media($xs) {
                margin-bottom: 20px;
            }
        }

        &-list {
            list-style: disc;
            color: $extra;

            @include media($lg) {
                display: flex;
                align-items: center;
                list-style: none;
            }
        }

        &-item:not(:last-child) {
            margin-bottom: 26px;

            @include media($lg) {
                margin-bottom: 0;
                margin-right: 64px;
            }

            @include media($md) {
                margin-right: 48px;
            }

            @include media($xs) {
                margin-right: 24px;
            }
        }

        &-link {
            @extend %footer-text;
            transition: 0.2s;

            &:hover {
                color: $extra_hover;
            }
        }
    }

    &__about {
        width: 462px;

        @include media($lg) {
            order: 1;
            width: 530px;
            display: flex;
        }

        @include media($md) {
            width: 460px;
        }

        @include media($sm) {
            width: 100%;
            flex-direction: column;
            align-items: center;
            text-align: center;
            order: 0;
            margin-bottom: 48px;
        }

        @include media($xs) {
            order: 1;
            margin-bottom: 0;
        }

        &-title {
            @extend %footer-title;
            margin-bottom: 64px;

            @include media($xl) {
                margin-bottom: 32px;
            }

            @include media($lg) {
                margin-bottom: 0;
                margin-right: 48px;
                min-width: fit-content;
            }

            @include media($md) {
                margin-right: 32px;
            }

            @include media($sm) {
                margin-right: 0;
                margin-bottom: 20px;
            }

            @include media($xs) {
                display: none;
            }
        }

        &-text {
            @extend %footer-text;

            @include media($lg) {
                line-height: 32px;
            }

            @include media($md) {
                line-height: 26px;
            }

            @include media($xs) {
                line-height: 22px;
                font-size: 15px;
                font-weight: 500;
            }
        }
    }

    &__contacts {
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        @include media($sm) {
            flex-direction: row;
            align-items: center;
            flex-wrap: wrap;
            justify-content: center;
        }

        @include media($xs) {
            justify-content: space-around;
            margin-bottom: 20px;
        }

        &-title {
            @extend %footer-title;
            margin-bottom: 38px;

            @include media($xl) {
                margin-bottom: 32px;
            }

            @include media($md) {
                margin-bottom: 24px;
            }

            @include media($sm) {
                min-width: 100%;
                text-align: center;
            }

            @include media($xs) {
                margin-bottom: 20px;
            }
        }

        &-link {
            @extend %footer-text;
            margin-bottom: 38px;
            transition: 0.2s;

            @include media($xl) {
                margin-bottom: 24px;
            }

            @include media($md) {
                margin-bottom: 16px;
            }

            @include media($sm) {
                margin-bottom: 24px;

                &:first-of-type {
                    margin-right: 48px;

                    @include media($xs) {
                        margin-right: 0;
                    }
                }
            }

            @include media($xs) {
                margin-bottom: 20px;
            }

            &:hover {
                color: $extra_hover;

                @include media($sm) {
                    color: initial;
                }
            }
        }

        &-socials {
            display: flex;
            align-items: center;

            a.footer__contacts-icon {
                font-size: 40px;
                transition: 0.2s;
                padding: 2px;

                &:not(:last-child) {
                    margin-right: 32px;

                    @include media($md) {
                        margin-right: 24px;
                    }

                    @include media($sm) {
                        margin-right: 48px;
                    }

                    @include media($sm) {
                        margin-right: 24px;
                    }
                }

                &:hover {
                    transform: scale(0.9);
                    opacity: 0.7;

                    @include media($sm) {
                        opacity: 1;
                        transform: none;
                    }
                }

                &::before {
                    @include media($xs) {
                        font-size: 35px;
                    }
                }
            }
        }
    }
}
