.printed {
    @include container;
    margin-bottom: 128px;
    position: relative;
    z-index: 1;

    @include media($md) {
        margin-bottom: 96px;
    }

    @include media($sm) {
        margin-bottom: 64px;
    }

    &__title {
        color: $text;
        font-size: 32px;
        font-weight: 600;
        margin-bottom: 96px;
        position: relative;
        width: fit-content;
        padding: 0 4px 12px;

        @include media($lg) {
            margin-bottom: 48px;
            font-size: 28px;
        }

        @include media($sm) {
            margin-bottom: 32px;
            font-size: 24px;
            padding: 0 2px 10px;
        }

        &::before {
            content: "";
            height: 7px;
            background: $backlight;
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            border-radius: 60px;

            @include media($sm) {
                height: 5px;
            }
        }
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 92px;

        @include media($lg) {
            gap: 48px;
        }

        @include media($md) {
            gap: 96px;
            grid-template-columns: repeat(2, 1fr);
        }

        @include media($sm) {
            gap: 32px;
            grid-template-columns: 1fr;
        }

        @include media($xs) {
            width: 320px;
            margin: 0 auto;
        }
    }

    &__item {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        border-radius: 47px;
        border: 2px solid $extra;
        overflow: hidden;

        &-img {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100%;
            background: $product-bg;

            img {
                max-width: 340px;
                max-height: 305px;
                height: auto;
                width: 100%;

                @include media($lg) {
                    max-width: 260px;
                }
            }
        }

        &-descr {
            padding-block: 24px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100%;
        }

        &-title {
            color: $text;
            font-size: 32px;
            font-weight: 600;
            margin-bottom: 64px;

            @include media($xl) {
                margin-bottom: 32px;
            }

            @include media($lg) {
                font-size: 28px;
            }
        }

        &-btn {
            padding: 24px 48px;
            font-family: "Montserrat";
            border-radius: 100px;
            background: $backlight;
            font-weight: 500;
            color: $btn-text;
            font-size: 16px;
            cursor: pointer;
            transition: 0.2s;

            @include media($lg) {
                padding: 20px 36px;
            }

            &:hover {
                opacity: 0.8;
            }
        }
    }
}

